.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}


.featured-item{
    flex: 1;
    margin: 0 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75 );
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75 );
}

.featured-title{
    font-size: 20px;
}

.featured_money-container{
    margin: 10px 0;
    display: flex;
    align-items: center;
}

.featured-money{
    font-size: 30px;
    font-weight: bold;
}

.featured_money-rate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featured-icon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}


.featured-icon.negative{
    color: red
}

.featured-sub{
    font-size: 15px;
    color: grey;
}